import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

import { compose } from 'redux';
import { connect } from 'react-redux';
import { firestoreConnect } from 'react-redux-firebase';

import Spinner from '../utilities/Spinner';

class Clients extends Component {
    state={
        totalOwed:null
    }

    //This is the new way to get props after clients are fetched
    static getDerivedStateFromProps(props, state) {
        const {clients} = props;

        if(clients){
            //Add balance
            const total = clients.reduce((total,client)=> {
                return total + parseFloat(client.balance.toString());
            },0);

            return {totalOwed:total}
        }

        return null;
    }

    render() {
        const {clients}= this.props;
        if(clients){
            return (
                <div>
                    <div className="row">
                        <div className="col-md-6">
                            <h2><i className="fas fa-users"></i> Clients</h2>
                        </div>
                        <div className="col-md-6">
                            <h5 classname="text-end text-secondary">
                                Total Owed{' : '}
                                <span className="text-primary">
                                    ${this.state.totalOwed}
                                </span>
                            </h5>
                        </div>
                    </div>
                    <table className="table table-striped">
                        <thead className="thead-inverse">
                            <tr>
                                <th>Name</th>
                                <th>Email</th>
                                <th>Balance</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {clients.map( client => (
                                <tr key={client.id}>
                                    <td>{client.firstName} {client.lastName}</td>
                                    <td>{client.email}</td>
                                    <td>${parseFloat(client.balance).toFixed(2)}</td>
                                    <td>
                                        <Link to={`/client/${client.id}`} className="btn btn-secondary btn-sm">
                                           <i className="fas fa-arrow-circle-right"></i> Details
                                        </Link>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            )
        }
        else{
            return <Spinner></Spinner>
        }
    }
}

Clients.propTypes={
    firestore:PropTypes.object.isRequired,
    clients:PropTypes.array
}
const mapStateToProps = state => {
    const clients=state.firestore.ordered.clients;
    return {
        clients:clients
    };
}
export default compose(
    connect(mapStateToProps),
    firestoreConnect(ownProps => [
        {collection:'clients'} //this will automatically sync our clients collection to firebase state
    ])
)(Clients);
